import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { AppSettings } from '../app-settings';
import { AlertService } from '../shared/alert/alert.service';
import { Observable } from "rxjs/Rx";

@Injectable()
export class ExtractionService {
    private api_url: string;
    private options: RequestOptions;
  
    extractFields(fieldsForm: any): Observable<any> {
      let formData = new FormData();
      let extractUrl = this.api_url + 'Extract/Fields';
       return this.http.post(extractUrl, formData, this.options)
            .map(res => res.json())
            .catch((error: any) => this.handleError(error));
    }

  constructor(private http: Http, private appSettings: AppSettings, private alertService: AlertService) {
        this.api_url = this.appSettings.api_url;
        let headers = new Headers({ 'Content-Type': 'multipart/form-data' });
        this.options = new RequestOptions({ headers: headers });
    }

    private extractData(res: Response) {
        const body = res.json();
        return body || {};
    }
    
    private handleError(error: any) {
        const errMsg = (error.message)
            ? error.message
            : error.status ? `${error.status} - ${error.statusText}` : "Server Error";
        this.alertService.error("Server Error", "Please consult an admin for help");
        return Observable.throw(errMsg);
    }

}



